@import "./base";
@import "./color";
.reset {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
}

html {
  @extend .reset;
  body {
    @extend .reset;
    font-size: 16px;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
      "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    background: $color-white-dark;
    #app {
      @extend .reset;
      .router {
        @extend .reset;
      }
    }
    .v-application--wrap {
      background: url("../../assets/images/background.gif");
    }
  }
}

/* Element UI 照片墙定位 */
.el-upload-list.el-upload-list--picture-card {
  display: block;
  margin: 0;
  padding: 0;
}

.el-loading-spinner {
  .el-icon-loading {
    font-size: 25vw;
    color: #9C27B0;
  }
  .el-loading-text {
    font-size: 20px !important;
    font-weight: bold;
    color: #9C27B0 !important;
  }
}

.el-upload {
  width: 100% !important;
  border: 1px dashed #c7000b !important;
  background: #fce4ec !important;
  i {
    color: #c7000b !important;
    font-weight: bold;
  }
  .el-upload--picture-card {
    display: block !important;
    width: 100% !important;
  }
}
